@value colors: "./styles/variables.css";
@value customPink, customGray, customGreen from colors;

@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&family=Roboto:wght@100;300;400;500;900&display=swap");
  

  
  body {
    padding: 0;
    background: rgb(250, 151, 151);
  }
  
  h2 {
    /* text-align: center; */
    color: rgb(154, 154, 154);
    /* text-transform: uppercase; */
    /* line-height: 100%; */
  }
  span {
    font-weight:bolder;
    font-size: 14px;
    font-family: "Montserrat";
  }
  p {
    color: rgb(154, 154, 154);
    font-family: "Montserrat";
    line-height: 200%;
    text-align: start;
  }
  .card-word {
    background-color: white;
    width: 1000px;
    display: grid;
    grid-template-columns: 3fr 2fr 5.5fr 1.5fr 3fr;
    grid-template-rows: 1fr;
    margin: 5px;
    border-radius: 10px;
    padding: 0px 30px;
    justify-content: left;
    gap: 20px;
  }
  .card-buttons {
    display: flex;
    flex-direction: row;
    gap: 10px;
    justify-content: flex-end;
    align-items: center;
    
  }
  

  .selected {
    background-color:rgb(253, 222, 222);
  }

  .cardSaveButton {
    height: 40px;
    width: 55px;
    background-color:#66dbba;
    border: none;
    border-radius: 5px;
    color:white;
    font-weight:800;
    font-size: 19px;
  }
.cardSaveButton:hover{
  background-color:#8ee1c9;
}
  .cardEditButton{
    height: 40px;
    width: 55px;
    background-color:#f7b276;
    color:white;
    border: none;
    border-radius: 5px;
    font-weight:800;
    font-size: 25px;
  }
  .cardEditButton:hover {
    background-color:#f8c89e;
  }

  .cardDeleteButton{
    height: 40px;
    width: 55px;
    background-color:rgb(253, 116, 104);
    color:white;
    border: none;
    border-radius: 5px;
    font-weight:800;
    font-size: 25px;
  }
  
  .cardDeleteButton:hover {
    background-color:rgb(254, 157, 148);
  }